  width: 254px;
  padding: 5px 8px 5px 9px;
  border-top: 1px solid #dbdbdb;
  border-bottom: 1px solid #dbdbdb;
  border-left: 1px solid #dbdbdb;
  border-right: 0;
  height: 18px;
  line-height: 100%;
  /* Aligns cursor to center */
  position: absolute;
  top: 0;
  left: 0px;
}
.btn.btn-default.search {
  width: 30px;
  height: 30px;
  position: absolute;
  left: 271px;
  border-top: 1px solid #dbdbdb;
  border-right: 1px solid #dbdbdb;
  border-bottom: 1px solid #dbdbdb;
  border-left: 0;
