  #navwrapper {
    /* Lower bottom border */
    height: 40px;
    position: relative;
    margin-top: 0px;
  }
  nav > ul {
    /* Nav mobile icon and background */
    display: none;
    height: auto;
    position: relative;
    /* IE7 */
    overflow: hidden;
    /* IE7 flicker */
  }
  nav > ul > li {
    display: block;
  }
  nav a#pull {
    /* use :after if position mobile icon */
    display: block;
    width: 100%;
    height: 40px;
    position: relative;
    background: #333333 url("../img/showmobileicon.png") center center no-repeat;
  }
  nav span {
    /* First level */
    /* Remove diagonal border - IE 7 */
    display: none;
  }
  nav ul ul {
    /* Second Level */
    width: 100%;
    position: relative;
    /* Stops overlap main menu */
  }
  nav ul ul li a {
    /* Font */
    text-align: center;
  }
  /* Services */
  #serviceswrapper {
    padding: 2%;
    width: 96%;
  }
  #services .box {
    height: auto;
    width: 49%;
    opacity: 1;
    overflow: visible;
  }
  #services .two {
    margin-left: 2%;
  }
