  #product_right,
  #product_left {
    width: 99%;
  }
  /* Content Three */
  #product3 .col4.last img {
    width: 50%;
    margin: 0 auto;
  }
  /* Footer */
  footer ul {
    text-align: center;
    margin: 10px 0px;
  }
  footer li a {
    padding-left: 0px;
    line-height: 20px;
    font-size: 14px;
    font-size: 12px;
  }
  #social {
    line-height: 70px;
    border: 0px;
    line-height: 50px;
  }
